import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

import HttpService from '../../services/HttpService';

const Container = styled.div`
    display: grid;
    grid-gap: 50px;
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: 
      "hd hd hd"
      "sd main main"
      ".  ft ft";
`
const Head = styled.div`
    grid-area: hd;
    align-content: center;
    text-align: center;
`;

const Card = styled.div`
    text-align: center;
    /* height: 320px;
    width: 240px; */
    grid-column-start: 1;
    grid-column-end: 2;
`;

const Details = styled.div`
    text-align: left;
    grid-column-start: 2;
    grid-column-end: 4;
    margin-right: 200px;
`;

const Video = styled.iframe`
    grid-row-end:5;
    height: 90%;
    width: 100%;
`;

class Movie extends React.Component {
    constructor(props) {
        super(props)
            this.state = {
            data: {},
            trailerFound: true
        }
    }
    
    componentWillMount() {
        const {match: {params: {movieId} } } = this.props; 
        HttpService.get(`/movie/${movieId}`, {language: 'en-US', append_to_response: 'videos'}).then(response => {
            // console.log(response.data);
            this.setState({
                data: response.data,
            });
            // console.log(this.state.data[0].title);
        }).catch((err) => {
            console.log(err.response);
        });

    }

    getTrailer() {
        if (this.state.data.videos !== undefined) {
            const videos = this.state.data.videos.results.find((x) => {
                return x.type === "Trailer";
            });
            if (videos === undefined) {
                this.setState({
                    trailerFound: false
                });
            } else {
                return videos.key;
            }
        }
    }

    render() {
        const imageURL = 'http://image.tmdb.org/t/p/w200';
        const movie = this.state.data;      
        console.log(movie);
        return (
            <Container>
                <Head>
                    <Link to='/'>
                        <h1>Movie Hunter</h1>
                    </Link>
                </Head>
                <Card>
                    <img src={imageURL+movie.poster_path} alt='loading'></img>
                    <p>{movie.title}</p>
                    <p>{movie.tagline}</p>
                </Card>
                <Details>
                    <p><strong>Synopsis: </strong>{movie.overview}</p>
                    <p>Released on: {movie.release_date}</p>
                    {movie.vote_count === 0 ? (
                        null
                    ) : (
                        <p>Rated {movie.vote_average} by {movie.vote_count} votes</p>
                    )}
                    {this.state.trailerFound ? (
                        <Video src={`https://www.youtube.com/embed/${this.getTrailer()}`}>

                        </Video>
                    ) : (
                        <p>Trailer not found</p>
                    )}
                </Details>
            </Container>
        )
    }
}

export default Movie;
