import React from 'react';
import styled from 'styled-components';

import MovieCard from '../MovieCard/';
import HttpService from '../../services/HttpService';

const FlexContainer = styled.ul`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: baseline;
    justify-content: center;
    align-content: center;
`;

class PopularMovies extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isLoading: false,
            page: 1,
            data: [],
        }
        this.handleScroll = this.handleScroll.bind(this);
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }
    
    handleScroll() {
        if(window.innerHeight + window.scrollY > (document.body.offsetHeight) && !this.state.isLoading)
            {
                const newPage = this.state.page+1;
                this.setState({isLoading: true, page: newPage});
                this.getMoreQueryResults();
            }
    }

    getMoreQueryResults() {
        HttpService.get('/movie/popular', {language: 'en-US', page: this.state.page}).then(response => {
            const results = this.state.data;
            this.setState({
                data: [...results, ...response.data.results],
                isLoading: false,
            });
            // console.log(this.state.data[0].title);
        }).catch((err) => {
            console.log(err.response);
        });
    }

    render() {

        return (
            <FlexContainer >
                {this.props.data.map((value) => (
                    <MovieCard data={value} />
                ))}
                {this.state.isLoading && <h1>Loading more movies</h1>}
                {this.state.data.map((value) => (
                    <MovieCard data={value} />
                ))}
            </FlexContainer>
        )
    }
}

export default PopularMovies;
